<template>
  <div>
    <DetailHead></DetailHead>
    <div class="infoBox">
      <div class="TitleBox">
        <span></span>
        <h2>{{Title}}</h2>
        <span></span>
      </div>
      <div class="sideNav">
        <el-menu
          default-active="1"
          class="el-menu-vertical-demo">
          <el-submenu :index="index+''" v-for="(value, index) in stones" :key="index" @click.native="changeTitle(value.title)">
            <template slot="title">
              <span>{{value.title}}</span>
            </template>
            <el-submenu :index="`${index}-${i}`" v-for="(value, i) in value.subTitle" :key="i"  @click.native="getInfo(value.name)">
              <template slot="title">
                <span>{{ value.name }}</span>
              </template>
              <el-menu-item :index="`${index}-${i}-${ind}`" v-for="(value, ind) in value.list" :key="ind" class="sItem" @click.native="getInfo(value)">{{value}}</el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </div>
      <div class="picShow">
        <vue-waterfall-easy :imgsArr="imgsArr"
                            :imgWidth='340'
                            :maxCols = 3
                            @scrollReachBottom="getInfo" >
          <div class="img-info" slot-scope="props">
            <p class="some-info infoTitle">{{props.value.intro}}</p>
          </div>
        </vue-waterfall-easy>
      </div>
    </div>
  </div>
</template>

<script>
import DetailHead from '@/components/DetailHead'
import { getClass } from '@/API'
import vueWaterfallEasy from 'vue-waterfall-easy'
export default {
  name: 'StoneDetail',
  components: {
    DetailHead,
    vueWaterfallEasy
  },
  created () {
    this.count = this.stones.indexOf(this.$store.state.stoneState)
    this.getInfo()
  },
  data () {
    return {
      className: '',
      Title: this.$store.state.stoneState,
      infoList: [],
      imgsArr: [], // 
      fetchImgsArr: [], // 
      stones: [
        {
          title: '',
          subTitle: [
            {
              name: '',
              list: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
            },
            {
              name: '-',
              list: ['', '', '', '']
            },
            {
              name: '',
              list: ['', '', '', '', '', '', '', '', '']
            },
            {
              name: '',
              list: ['', '', '', '', '']
            },
            {
              name: '',
              list: ['', '']
            }
          ]
        },
        {
          title: '',
          subTitle: [
            {
              name: '',
              list: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '()', '', '', '',
                '', '', '', '', '', '', '']
            },
            {
              name: '',
              list: ['', '', '', '', '', '', '', '', '', '', '', '', '', '',
                '', '', '', '', '', '', '', '', '', '', '', '', '',
                '', '', '', '']
            },
            {
              name: '',
              list: ['', '', '', '', '', '', '', '', '', '', '', '', '', '',
                '', '']
            }
          ]
        },
        {
          title: '',
          subTitle: [
            {
              name: '',
              list: []
            },
            {
              name: '',
              list: []
            },
            {
              name: '',
              list: []
            }]
        },
        {
          title: '',
          subTitle: [
            {
              name: '',
              list: ['', '', '']
            },
            {
              name: '',
              list: ['', '', '', '']
            }]
        }
      ]
    }
  },
  methods: {
    getInfo: function (value) {
      if (value !== this.className) {
        this.className = value
        getClass({ class: this.className }).then((data) => {
          this.infoList = data.data
          console.log(data)
          this.imgsArr = data.data
        })
          .catch(function (err) {
            console.log(err)
          })
      }
    },
    changeTitle (title) {
      this.Title = title
    }
  }
}
</script>

<style scoped lang="scss">
.picShow{
  width: 1400px;
  float: right;
  height: 1400px;
  margin-top: 56px;
}
.TitleBox{
  margin: 0 auto;
  display: flex;
  width: 440px;
  justify-content: space-between;
  align-items: center;
  height: 67px;
  span{
    display: inline-block;
    width: 80px;
    height: 12px;
    background: #429070;
  }
  h2{
    width: 200px;
    font-size: 50px;
    text-align: center;
  }
}
.Detail{
  background-color: #e4e4e4;
}
.infoBox{
  height: 2300px;
  width: 1700px;
  margin:73px auto 0 auto;
  overflow: hidden;
}
.infoTitle{
  padding: 10px 20px;
  height: 70px;
  background-color: whitesmoke;
  box-sizing: border-box;
  border-radius: 10px;
  font-size: 18px;
}
.img-box{
  border-radius: 10px;
}
.Detail{
  background-color: #e4e4e4;
}
.words{
  background-color: #cbcbcb;
  padding: 12px 18px;
  box-sizing: border-box;
  overflow: hidden;
}
.Detail{
  position: relative;
}
.sideNav{
  width: 240px;
  float: left;
  margin-top: 66px;
}

</style>
