<template>
    <div id="china_map" style='width:800px;height:600px;'  ref="ChinaMap"> </div>
</template>

<script>
import 'echarts/map/js/china'
const echarts = require('echarts')
export default {
  name: 'Map',
  mounted () {
    const _this = this
    this.$nextTick(() => {
      _this.chinachart = echarts.init(document.getElementById('china_map'))
      _this.chinachart.on('click', function (param) {
        console.log(param)
        _this.$store.state.region = param.name
        console.log(_this.$store.state.region)
        _this.$router.push('/RegionDetail')
      })
      // echarts
      // 
      _this.chartOption = {
        geo: { // 
          map: 'china', // 
          // roam: true, // 
          zoom: 1.2, // 
          label: {
            show: true
          },
          itemStyle: { //  
            borderColor: 'rgba(0, 0, 0, 0.2)',
            emphasis: { // 
              areaColor: 'green',
              shadowBlur: 20,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }
      }
      // 
      _this.chinachart.setOption(this.chartOption)
    })
  },
  methods: {
    show () {
      this.chinachart.on('click', function (params) {
        console.log(params)
      })
    }
  },
  data () {
    return {
      value1: true,
      value2: true
    }
  }

}
</script>

