<script>
// import vueWaterfallEasy from 'vue-waterfall-easy'
import { getClass } from '@/API'
export default {
  name: 'MobileRegionDetail',
  created () {
    this.getData()
  },
  data () {
    return {
      isClick: true,
      Title: this.$store.state.region,
      imgsArr: [], // 
      fetchImgsArr: [], // 
      obj: {
        class: '',
        page: 0,
        nums: 99
      }
    }
  },
  methods: {
    changeRegion (region) {
      if (region !== this.obj.class) {
        this.Title = region
        this.obj.class = region
        console.log(this.obj)
        this.getData()
      }
    },
    getData () {
      getClass(this.obj)
        .then(res => {
          if (this.obj.page === 1) { //  slot="waterfall-over"
            this.$refs.waterfall.waterfallOver()
            return
          }
          this.imgsArr = res.data
          console.log(this.imgsArr)
        })
        .catch(
          this.imgsArr = []
        )
    },
    fatherSelected (id) {
      this.$router.push({
        path: `/RegionDetail/detail/${id}`
      })
    },
    clickFn (event, { index, value }) {
      // a
      event.preventDefault()
      // 
      if (event.target.tagName.toLowerCase() === 'img') {
        this.fatherSelected()
      }
    }
  },
  components: {
    // vueWaterfallEasy
  }
}
</script>
